import React from 'react'

const NotFoundPage = () => (
  <div>
    <h1>DON'T DO THAT</h1>
    <p>Are you just trying random URLs? There's nothing here, and I assume it's because you did something wrong, not me.</p>
  </div>
)

export default NotFoundPage



